@keyframes growBand {
    0% {
        opacity: 1;
        z-index: 1;
        min-height: 0;
    }

    50% {
        opacity: 0;
        z-index: 1;
        min-height: 550px;
    }

    100% {
        opacity: 0;
        z-index: -1;
        min-height: 550px;
    }
}

@keyframes shrinkBand {
    0% {
        opacity: 1;
        z-index: 1;
        max-height: 550px;
    }

    50% {
        opacity: 0;
        z-index: -1;
        max-height: 550px;
    }

    100% {
        opacity: 0;
        z-index: -1;
        max-height: 50px;
    }
}

@keyframes opacity {
    50% {
        opacity: 0;
        z-index: -1;
    }

    100% {
        opacity: 1;
        z-index: 1;
    }
}

@keyframes fadeIn {
    0% {
      opacity: 0;
    }
  
    100% {
      opacity: 1;
    }
  }
  
  @keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
    }
  
    100% {
      opacity: 1;
      transform: none;
    }
  }
  
  @keyframes fadeInRight {
    0% {
      opacity: 0;
      transform: translate3d(100%, 0, 0);
    }
  
    100% {
      opacity: 1;
      transform: none;
    }
  }

.loading_cont {
    position: fixed;
    width: 100vw;
    height: 100vh;
}

#loader {
    display: inline-block;
    position: relative;
    width: 100vw;
    height: 100vh;
}

.blobs {
    filter: url("#goo");
}

.blob {
    fill: #fff;
}